@import './fonts';
@import 'src/styles/variables';

* {
	box-sizing: border-box;
}

html,
body,
#__nuxt,
#__layout {
	height: 100%;
}

body {
	margin: 0;
	color: $c-black;
	overflow-x: hidden;

	font-family: $font-family-body;
	font-weight: 400;
	font-size: calc(12px + .8vw);
}

a {
	position: relative;

	color: inherit;
	text-decoration: none;
}

.icon {
	height: 100%;
	width: 100%;
}

button,
input,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
	outline: none;
	border: none;
	padding: 0;
	margin: 0;

	background: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	//font-family: $font-family-heading;
}

li {
	list-style-type: none;
}

ul {
	margin-left: 0;
	padding-left: 0;
}

.body--hidden {
	overflow: hidden !important;
}

.link {
	box-shadow: inset 0 -0.4em 0 0 $c-blue;
	transition: box-shadow $duration ease-in-out;
	outline-offset: 0.1em;

	&:hover {
		box-shadow: inset 0 -1.2em 0 0 $c-blue;
	}
}
